import { useState, useEffect } from "react";
import { Howl, Howler } from "howler";
import popupSound1 from "./media/AVARD-AUDIO.mp3";
import popupSound2 from "./media/brendan-audio.mp3";
import popupSound3 from "./media/PETER-AUDIO.mp3";
import popupSound4 from "./media/yeti-audio.mp3";
import popupSound5 from "./media/dorthy-audio.mp3";

const soundFiles = {
  1: popupSound1,
  2: popupSound2,
  3: popupSound3,
  4: popupSound4,
  5: popupSound5,
};

const usePopupSound = () => {
  const [volume, setVolume] = useState(0.3);
  const [sounds, setSounds] = useState({});

  // Preload sounds
  useEffect(() => {
    const loadedSounds = {};
    Object.entries(soundFiles).forEach(([key, path]) => {
      loadedSounds[key] = new Howl({
        src: [path],
        volume: volume,
      });
    });
    setSounds(loadedSounds);

    return () => {
      Object.values(loadedSounds).forEach((sound) => {
        sound.unload();
      });
    };
  }, [volume]);

  const playPopupSound = (soundId) => {
    if (sounds[soundId]) {
      sounds[soundId].stop();
      sounds[soundId].play();
    }
  };

  // Set the volume level
  const setCurrentVolume = (vol) => {
    setVolume(vol);
    Howler.volume(vol);
  };

  return { playPopupSound, setCurrentVolume };
};

export default usePopupSound;
